___CSS_LOADER_EXPORT___.push([module.id, "\n.panel[data-v-42c7142d] {\r\n  box-sizing: border-box;\r\n  padding: 16px;\r\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\r\n  background: #f5f5f5;\r\n  min-height: 100vh;\r\n  background: url(" + ___CSS_LOADER_URL_REPLACEMENT_0___ + ") repeat-y;\r\n  width: 960px;\r\n  margin: 0 auto;\n}\n.panel-header[data-v-42c7142d] {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 20px;\r\n  padding-bottom: 12px;\r\n  border-bottom: 1px solid #e0e0e0;\n}\n.panel-header h3[data-v-42c7142d] {\r\n  margin: 0;\r\n  color: #333;\r\n  font-size: 18px;\n}\n.connection-status[data-v-42c7142d] {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\n}\n.status-indicator[data-v-42c7142d] {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\n}\n.status-dot[data-v-42c7142d] {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 4px;\r\n  height: 4px;\r\n  border-radius: 50%;\r\n  display: inline-block;\r\n  transition: all 0.3s ease;\n}\n.status-dot.connected[data-v-42c7142d] {\r\n  background-color: #52c41a;\r\n  animation: pulse-green-strong-42c7142d 1.5s infinite;\n}\n.status-dot.disconnected[data-v-42c7142d] {\r\n  background-color: #faad14;\r\n  animation: pulse-orange-strong-42c7142d 1s infinite;\n}\r\n\r\n/*  */\n@keyframes pulse-green-strong-42c7142d {\n0% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    box-shadow: 0 0 0 0 rgba(82, 196, 26, 0.7);\r\n    opacity: 1;\n}\n50% {\r\n    transform: translate(-50%, -50%) scale(1.2);\r\n    box-shadow: 0 0 0 10px rgba(82, 196, 26, 0);\r\n    opacity: 0.8;\n}\n100% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    box-shadow: 0 0 0 0 rgba(82, 196, 26, 0.7);\r\n    opacity: 1;\n}\n}\r\n\r\n/*  */\n@keyframes pulse-orange-strong-42c7142d {\n0% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    box-shadow: 0 0 0 0 rgba(250, 173, 20, 0.7);\r\n    opacity: 1;\n}\n25% {\r\n    transform: translate(-50%, -50%) scale(1.3);\r\n    box-shadow: 0 0 0 8px rgba(250, 173, 20, 0.4);\r\n    opacity: 0.6;\n}\n50% {\r\n    transform: translate(-50%, -50%) scale(1.1);\r\n    box-shadow: 0 0 0 15px rgba(250, 173, 20, 0);\r\n    opacity: 0.8;\n}\n75% {\r\n    transform: translate(-50%, -50%) scale(1.2);\r\n    box-shadow: 0 0 0 5px rgba(250, 173, 20, 0.2);\r\n    opacity: 0.7;\n}\n100% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    box-shadow: 0 0 0 0 rgba(250, 173, 20, 0.7);\r\n    opacity: 1;\n}\n}\n.status-text[data-v-42c7142d] {\r\n  font-size: 12px;\r\n  color: #666;\r\n  font-weight: 500;\n}\n.mode-indicator[data-v-42c7142d] {\r\n  font-size: 10px;\r\n  padding: 2px 6px;\r\n  border-radius: 3px;\r\n  font-weight: bold;\r\n  margin-left: 8px;\n}\n.mode-indicator.sidepanel[data-v-42c7142d] {\r\n  background-color: #1890ff;\r\n  color: white;\n}\n.mode-indicator.new-window[data-v-42c7142d] {\r\n  background-color: #52c41a;\r\n  color: white;\n}\n.new-window-tip[data-v-42c7142d] {\r\n  margin-bottom: 16px;\r\n  border-radius: 6px;\n}\n.new-window-tip p[data-v-42c7142d] {\r\n  margin: 4px 0;\r\n  font-size: 12px;\r\n  line-height: 1.4;\n}\n.sidebar-tip[data-v-42c7142d] {\r\n  margin-bottom: 16px;\r\n  border-radius: 6px;\n}\n.sidebar-tip p[data-v-42c7142d] {\r\n  margin: 4px 0;\r\n  font-size: 12px;\r\n  line-height: 1.4;\n}\n.data-section h4[data-v-42c7142d] {\r\n  margin: 0 0 12px 0;\r\n  color: #666;\r\n  font-size: 14px;\n}\n.empty-state[data-v-42c7142d] {\r\n  text-align: center;\r\n  padding: 40px 20px;\r\n  color: #999;\r\n  background: white;\r\n  border-radius: 4px;\r\n  border: 1px dashed #ddd;\n}\n.request-list[data-v-42c7142d] {\r\n  background: white;\r\n  border-radius: 4px;\r\n  border: 1px solid #e0e0e0;\r\n  overflow: hidden;\n}\n.request-item[data-v-42c7142d] {\r\n  border-bottom: 1px solid #f0f0f0;\r\n  padding: 12px 16px;\r\n  transition: background-color 0.2s;\n}\n.request-item[data-v-42c7142d]:last-child {\r\n  border-bottom: none;\n}\n.request-item[data-v-42c7142d]:hover {\r\n  background-color: #fafafa;\n}\n.request-item.completed[data-v-42c7142d] {\r\n  background-color: #f0f9ff;\r\n  border-left: 3px solid #1890ff;\n}\n.request-info[data-v-42c7142d] {\r\n  margin-bottom: 8px;\n}\n.request-url[data-v-42c7142d] {\r\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\r\n  font-size: 12px;\r\n  color: #333;\r\n  word-break: break-all;\r\n  margin-bottom: 4px;\n}\n.request-meta[data-v-42c7142d] {\r\n  display: flex;\r\n  gap: 12px;\r\n  font-size: 11px;\n}\n.method[data-v-42c7142d] {\r\n  background: #1890ff;\r\n  color: white;\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\n}\n.status[data-v-42c7142d] {\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\n}\n.status.pending[data-v-42c7142d] {\r\n  background: #faad14;\r\n  color: white;\n}\n.status.completed[data-v-42c7142d] {\r\n  background: #52c41a;\r\n  color: white;\n}\n.timestamp[data-v-42c7142d] {\r\n  color: #999;\n}\n.response-data[data-v-42c7142d] {\r\n  margin-top: 8px;\r\n  padding-top: 8px;\r\n  border-top: 1px solid #f0f0f0;\n}\n.response-content[data-v-42c7142d] {\r\n  margin-top: 8px;\r\n  background: #f8f8f8;\r\n  border-radius: 4px;\r\n  padding: 8px;\r\n  max-height: 300px;\r\n  overflow-y: auto;\n}\n.response-content pre[data-v-42c7142d] {\r\n  margin: 0;\r\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\r\n  font-size: 11px;\r\n  line-height: 1.4;\r\n  color: #333;\r\n  white-space: pre-wrap;\r\n  word-break: break-word;\n}\n.role-card[data-v-42c7142d] .el-card__body {\r\n  padding: 8px;\n}\r\n\r\n/*  */\n.role-card.empty-role[data-v-42c7142d] {\r\n  opacity: 0.6;\r\n  filter: grayscale(0.8);\r\n  background-color: #f5f5f5;\r\n  border: 1px solid #d9d9d9;\r\n  transition: all 0.3s ease;\n}\n.role-card.empty-role[data-v-42c7142d]:hover {\r\n  opacity: 0.8;\r\n  filter: grayscale(0.6);\n}\n.role-card.empty-role[data-v-42c7142d] .el-card__body {\r\n  background-color: #fafafa;\n}\r\n\r\n/*  */\n.role-card.empty-role .el-tag[data-v-42c7142d] {\r\n  opacity: 0.7;\n}\n.role-card.empty-role .el-link[data-v-42c7142d] {\r\n  opacity: 0.7;\n}\n.role-card.empty-role span[data-v-42c7142d] {\r\n  opacity: 0.7;\n}\r\n", "",{"version":3,"sources":["webpack://./src/chrome-extensions/DevToolsPanel.vue"],"names":[],"mappings":";AA+uBA;EACA,sBAAA;EACA,aAAA;EACA,gFAAA;EACA,mBAAA;EACA,iBAAA;EACA,4DAAA;EACA,YAAA;EACA,cAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,mBAAA;EACA,oBAAA;EACA,gCAAA;AACA;AAEA;EACA,SAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,SAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,QAAA;AACA;AAEA;EACA,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EACA,UAAA;EACA,WAAA;EACA,kBAAA;EACA,qBAAA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,oDAAA;AACA;AAEA;EACA,yBAAA;EACA,mDAAA;AACA;;AAEA,aAAA;AACA;AACA;IACA,yCAAA;IACA,0CAAA;IACA,UAAA;AACA;AAEA;IACA,2CAAA;IACA,2CAAA;IACA,YAAA;AACA;AAEA;IACA,yCAAA;IACA,0CAAA;IACA,UAAA;AACA;AACA;;AAEA,aAAA;AACA;AACA;IACA,yCAAA;IACA,2CAAA;IACA,UAAA;AACA;AAEA;IACA,2CAAA;IACA,6CAAA;IACA,YAAA;AACA;AAEA;IACA,2CAAA;IACA,4CAAA;IACA,YAAA;AACA;AAEA;IACA,2CAAA;IACA,6CAAA;IACA,YAAA;AACA;AAEA;IACA,yCAAA;IACA,2CAAA;IACA,UAAA;AACA;AACA;AAEA;EACA,eAAA;EACA,WAAA;EACA,gBAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;AACA;AAEA;EACA,yBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,WAAA;EACA,eAAA;AACA;AAEA;EACA,kBAAA;EACA,kBAAA;EACA,WAAA;EACA,iBAAA;EACA,kBAAA;EACA,uBAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;EACA,yBAAA;EACA,gBAAA;AACA;AAEA;EACA,gCAAA;EACA,kBAAA;EACA,iCAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;EACA,8BAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,wDAAA;EACA,eAAA;EACA,WAAA;EACA,qBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,gBAAA;EACA,kBAAA;EACA,iBAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;EACA,YAAA;AACA;AAEA;EACA,WAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,6BAAA;AACA;AAEA;EACA,eAAA;EACA,mBAAA;EACA,kBAAA;EACA,YAAA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,SAAA;EACA,wDAAA;EACA,eAAA;EACA,gBAAA;EACA,WAAA;EACA,qBAAA;EACA,sBAAA;AACA;AAEA;EACA,YAAA;AACA;;AAEA,aAAA;AACA;EACA,YAAA;EACA,sBAAA;EACA,yBAAA;EACA,yBAAA;EACA,yBAAA;AACA;AAEA;EACA,YAAA;EACA,sBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,gBAAA;AACA;EACA,YAAA;AACA;AAEA;EACA,YAAA;AACA;AAEA;EACA,YAAA;AACA","sourcesContent":["<template>\r\n  <div class=\"panel\">\r\n    <div class=\"panel-header\">\r\n      <el-row type=\"flex\" align=\"middle\">\r\n        <div style=\"width: 32px;height: 32px;margin-right: 10px;position: relative;\">\r\n          <img src=\"~@/assets/logo.png\" alt=\"\" style=\"width: 32px;height: 32px;\">\r\n          <span class=\"status-dot\"\r\n            :class=\"{ 'connected': devtoolsConnected, 'disconnected': !devtoolsConnected }\"></span>\r\n        </div>\r\n        <h3 style=\"color: #fff;\"></h3>\r\n      </el-row>\r\n      <div class=\"connection-status\">\r\n        <div class=\"pages\"><a href=\"javascript:goto(1)\" @click=\"prevPage\"></a><a href=\"javascript:goto(1)\"\r\n            @click=\"nextPage\"></a></div>\r\n        <el-button @click=\"getPageInfo\" size=\"mini\" type=\"info\"></el-button>\r\n        <el-button @click=\"reconnectDevTools\" size=\"mini\" type=\"warning\" v-if=\"!devtoolsConnected\"></el-button>\r\n        <a v-if=\"!isInNewWindow\" href=\"javascript:;\" class=\" btn1 js_alert_btn_0\" @click=\"openInNewTab\"></a>\r\n        <a href=\"javascript:;\" class=\" btn1 js_alert_btn_0\" @click=\"clearData\"></a>\r\n      </div>\r\n    </div>\r\n    <div class=\"data-section\">\r\n      <el-empty v-if=\"recommendData.length === 0\" class=\"empty-state\" description=\"\"></el-empty>\r\n      <div v-else class=\"request-list\">\r\n        <div v-for=\"item in recommendData\" :key=\"item.requestId\" class=\"request-item\"\r\n          :class=\"{ 'completed': item.status === 'completed' }\">\r\n          <div class=\"request-info\">\r\n            <div class=\"request-meta\">\r\n              <span class=\"status\" :class=\"item.status\">{{ item.status === 'completed' ? '' : '' }}</span>\r\n              <span class=\"timestamp\">{{ formatTime(item.timestamp) }}</span>\r\n            </div>\r\n          </div>\r\n          <div v-if=\"item.responseData\" class=\"response-data\">\r\n            <el-row :gutter=\"4\">\r\n              <el-col v-for=\"role in parseListData(item.responseData)?.equip_list\" :key=\"role.eid\"\r\n                style=\"width: 20%;margin-bottom: 2px;margin-top: 2px;\">\r\n                <el-card class=\"role-card\" :class=\"{ 'empty-role': isEmptyRole(parserRoleData(role)) }\">\r\n                  <el-row type=\"flex\" justify=\"space-between\">\r\n                    <el-col style=\"width:50px;flex-shrink: 0;margin-right: 4px;\">\r\n                      <RoleImage :key=\"role.eid\" :other_info=\"role.other_info\" :roleInfo=\"parserRoleData(role)\" />\r\n                      <el-link :href=\"getCBGLinkByType(role.eid, 'role')\" type=\"danger\" target=\"_blank\"\r\n                        style=\"white-space: nowrap;text-overflow: ellipsis;overflow: hidden;display: block;font-size: 12px;\">\r\n                        {{ role.seller_nickname }}</el-link>\r\n                    </el-col>\r\n                    <el-col>\r\n                      <div>\r\n                        <el-tag type=\"success\" v-if=\"role.accept_bargain == 1\"></el-tag>\r\n                        <el-tag type=\"danger\" v-else></el-tag>\r\n                      </div>\r\n                      <div>\r\n                        <span v-html=\"formatFullPrice(role.price, true)\"></span>\r\n                      </div>\r\n                      <div>\r\n                        <el-tag type=\"danger\" v-if=\"isEmptyRole(parserRoleData(role))\"></el-tag>\r\n                        <template v-else>\r\n                          <el-tag> {{ get_equip_num(parserRoleData(role)) }}</el-tag>\r\n                          <el-tag type=\"success\"> {{ get_pet_num(parserRoleData(role)) }}</el-tag>\r\n                        </template>\r\n                      </div>\r\n                      <div>\r\n                        <SimilarRoleModal :role=\"role\"\r\n                          :search-params=\"{ selectedDate: selectedDate, roleType: 'normal' }\">\r\n                          <div> <el-link type=\"primary\" href=\"javascript:void(0)\"> </el-link></div>\r\n                        </SimilarRoleModal>\r\n                      </div>\r\n                    </el-col>\r\n                  </el-row>\r\n                </el-card>\r\n              </el-col>\r\n            </el-row>\r\n            <el-button @click=\"toggleResponse(index)\" size=\"mini\" type=\"text\">\r\n              {{ expandedItems.includes(index) ? '' : '' }}\r\n            </el-button>\r\n            <div v-if=\"expandedItems.includes(index)\" class=\"response-content\">\r\n              <pre>{{ JSON.stringify(item.responseData, null, 2) }}</pre>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nimport dayjs from 'dayjs'\r\nimport RoleImage from '@/components/RoleInfo/RoleImage.vue'\r\nimport SimilarRoleModal from '@/components/SimilarRoleModal.vue'\r\nimport { commonMixin } from '@/utils/mixins/commonMixin'\r\nexport default {\r\n  name: 'DevToolsPanel',\r\n  data() {\r\n    return {\r\n      selectedDate: dayjs().format('YYYY-MM'),\r\n      recommendData: [],\r\n      expandedItems: [],\r\n      processedRequests: new Set(), // ID\r\n      devtoolsConnected: false, // DevTools Protocol\r\n      connectionStatus: '...', // \r\n      connectionCheckTimer: null, // \r\n      isInNewWindow: false // \r\n    }\r\n  },\r\n  mixins: [commonMixin],\r\n  components: {\r\n    RoleImage,\r\n    SimilarRoleModal\r\n  },\r\n  computed: {\r\n\r\n  },\r\n  mounted() {\r\n    this.initMessageListener()\r\n    this.checkConnectionStatus()\r\n    this.checkIfInNewWindow()\r\n\r\n    // // 5\r\n    // this.connectionCheckTimer = setInterval(() => {\r\n    //   this.checkConnectionStatus()\r\n    // }, 5000)\r\n  },\r\n  beforeDestroy() {\r\n    // Chrome\r\n    this.removeMessageListener()\r\n    // \r\n    if (this.connectionCheckTimer) {\r\n      clearInterval(this.connectionCheckTimer)\r\n      this.connectionCheckTimer = null\r\n    }\r\n    // \r\n    this.recommendData = []\r\n    this.expandedItems = []\r\n  },\r\n  methods: {\r\n    isEmptyRole(roleInfo) {\r\n      const noEquip = this.get_equip_num(roleInfo) === 0\r\n      let noPet = true\r\n      for (let pet of roleInfo.pet_info) {\r\n        if (pet.pet_grade > 100 && pet.is_baobao === '') {\r\n          noPet = false\r\n          break\r\n        }\r\n        if (pet.pet_grade > 100 && pet.is_baobao === '' && pet.all_skills.length > 4) {\r\n          noPet = false\r\n          break\r\n        }\r\n      }\r\n      return noEquip && noPet\r\n    },\r\n    get_pet_num(roleInfo) {\r\n      return roleInfo.pet_info.length + roleInfo.split_pets.length\r\n    },\r\n    get_equip_num(roleInfo) {\r\n      return roleInfo.using_equips.length + roleInfo.not_using_equips.length + roleInfo.split_equips.length\r\n    },\r\n    nextPage() {\r\n      // ChromeAPI\r\n      this.clickPageButton('next')\r\n    },\r\n\r\n    prevPage() {\r\n      // ChromeAPI\r\n      this.clickPageButton('prev')\r\n    },\r\n\r\n    getPageInfo() {\r\n      // \r\n      this.getPagerInfo()\r\n    },\r\n\r\n    reconnectDevTools() {\r\n      // DevTools\r\n      this.connectionStatus = '...'\r\n      this.checkConnectionStatus()\r\n      this.$notify.info('DevTools...')\r\n    },\r\n\r\n    async clickPageButton(direction) {\r\n      try {\r\n        // \r\n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\r\n\r\n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\r\n          this.$notify.warning('')\r\n          return\r\n        }\r\n\r\n        // ChromeAPI\r\n        if (!this.devtoolsConnected) {\r\n          this.$notify.warning('DevTools')\r\n          return\r\n        }\r\n\r\n        // ChromeAPIJavaScript\r\n        const result = await chrome.debugger.sendCommand(\r\n          { tabId: activeTab.id },\r\n          'Runtime.evaluate',\r\n          {\r\n            expression: `\r\n              (function() {\r\n                try {\r\n                  // idpagerdiv\r\n                  const pagerDiv = document.getElementById('pager')\r\n                  if (!pagerDiv) {\r\n                    return 'ERROR:'\r\n                  }\r\n                  \r\n                  let targetButton = null\r\n                  const isNext = '${direction}' === 'next'\r\n                  \r\n                  if (isNext) {\r\n                    //  - HTML\r\n                    // 1. \"\"\r\n                    const allLinks = pagerDiv.querySelectorAll('a')\r\n                    for (let link of allLinks) {\r\n                      const text = link.textContent.trim()\r\n                      if (text === '') {\r\n                        targetButton = link\r\n                        break\r\n                      }\r\n                    }\r\n                    \r\n                    // 2. \"\"goto\r\n                    if (!targetButton) {\r\n                      for (let link of allLinks) {\r\n                        const href = link.getAttribute('href')\r\n                        const text = link.textContent.trim()\r\n                        // goto\r\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\r\n                          // \r\n                          const currentPageLink = pagerDiv.querySelector('a.on')\r\n                          if (currentPageLink) {\r\n                            const currentPageText = currentPageLink.textContent.trim()\r\n                            const currentPage = parseInt(currentPageText)\r\n                            const linkPage = parseInt(text)\r\n                            // \r\n                            if (!isNaN(linkPage) && linkPage > currentPage) {\r\n                              targetButton = link\r\n                              break\r\n                            }\r\n                          }\r\n                        }\r\n                      }\r\n                    }\r\n                  } else {\r\n                    // \r\n                    const allLinks = pagerDiv.querySelectorAll('a')\r\n                    \r\n                    // 1. \"\"\r\n                    for (let link of allLinks) {\r\n                      const text = link.textContent.trim()\r\n                      if (text === '') {\r\n                        targetButton = link\r\n                        break\r\n                      }\r\n                    }\r\n                    \r\n                    // 2. \"\"goto\r\n                    if (!targetButton) {\r\n                      for (let link of allLinks) {\r\n                        const href = link.getAttribute('href')\r\n                        const text = link.textContent.trim()\r\n                        // goto\r\n                        if (href && href.includes('goto(') && !link.classList.contains('on')) {\r\n                          // \r\n                          const currentPageLink = pagerDiv.querySelector('a.on')\r\n                          if (currentPageLink) {\r\n                            const currentPageText = currentPageLink.textContent.trim()\r\n                            const currentPage = parseInt(currentPageText)\r\n                            const linkPage = parseInt(text)\r\n                            // \r\n                            if (!isNaN(linkPage) && linkPage < currentPage) {\r\n                              targetButton = link\r\n                              break\r\n                            }\r\n                          }\r\n                        }\r\n                      }\r\n                    }\r\n                  }\r\n                  \r\n                  if (!targetButton) {\r\n                    return 'ERROR:${direction === 'next' ? '' : ''}'\r\n                  }\r\n                  \r\n                  // \r\n                  if (targetButton.disabled || targetButton.classList.contains('disabled')) {\r\n                    return 'ERROR:${direction === 'next' ? '' : ''}${direction === 'next' ? '' : ''}'\r\n                  }\r\n                  \r\n                  // \r\n                  const currentPageLink = pagerDiv.querySelector('a.on')\r\n                  let currentPageInfo = ''\r\n                  if (currentPageLink) {\r\n                    const currentPageText = currentPageLink.textContent.trim()\r\n                    currentPageInfo = ' (' + currentPageText + ')'\r\n                  }\r\n                  \r\n                  // \r\n                  targetButton.click()\r\n                  return 'SUCCESS:${direction === 'next' ? '' : ''}' + currentPageInfo\r\n                } catch (error) {\r\n                  return 'ERROR: - ' + error.message\r\n                }\r\n              })()\r\n            `\r\n          }\r\n        )\r\n\r\n        // ChromeAPI\r\n        if (result && result.result && result.result.value) {\r\n          const message = result.result.value\r\n\r\n          if (message.startsWith('SUCCESS:')) {\r\n            this.$notify.success(message.substring(8)) // \"SUCCESS:\"\r\n            console.log(`${direction === 'next' ? '' : ''}`)\r\n          } else if (message.startsWith('ERROR:')) {\r\n            this.$notify.warning(message.substring(6)) // \"ERROR:\"\r\n            console.warn(`${direction === 'next' ? '' : ''}:`, message)\r\n          } else {\r\n            this.$notify.error('')\r\n            console.error(':', result)\r\n          }\r\n        } else {\r\n          this.$notify.error('')\r\n          console.error(':', result)\r\n        }\r\n\r\n      } catch (error) {\r\n        console.error(`${direction === 'next' ? '' : ''}:`, error)\r\n\r\n        // \r\n        if (error.message && error.message.includes('Could not establish connection')) {\r\n          this.devtoolsConnected = false\r\n          this.connectionStatus = ''\r\n          this.$notify.error('DevTools')\r\n        } else {\r\n          this.$notify.error(': ' + error.message)\r\n        }\r\n      }\r\n    },\r\n\r\n    async getPagerInfo() {\r\n      try {\r\n        // \r\n        const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })\r\n\r\n        if (!activeTab || !activeTab.url.includes('cbg.163.com')) {\r\n          this.$notify.warning('')\r\n          return\r\n        }\r\n\r\n        // ChromeAPI\r\n        if (!this.devtoolsConnected) {\r\n          this.$notify.warning('DevTools')\r\n          return\r\n        }\r\n\r\n        // ChromeAPIJavaScript\r\n        //pagerDivinnerText `100`100\r\n        const result = await chrome.debugger.sendCommand(\r\n          { tabId: activeTab.id },\r\n          'Runtime.evaluate',\r\n          {\r\n            expression: `\r\n              (function() {\r\n                try {\r\n                  // idpagerdiv\r\n                  const pagerDiv = document.getElementById('pager')\r\n                  if (!pagerDiv) {\r\n                    return 'ERROR:'\r\n                  }\r\n                  \r\n                  // \r\n                  const currentPageLink = pagerDiv.querySelector('a.on')\r\n                  let currentPage = ''\r\n                  if (currentPageLink) {\r\n                    currentPage = currentPageLink.textContent.trim()\r\n                  }\r\n                  \r\n                  // innerText\"X\"\r\n                  let totalPages = ''\r\n                  const innerText = pagerDiv.innerText || pagerDiv.textContent || ''\r\n                  \r\n                  // \"\"\"\"\r\n                  const gongIndex = innerText.indexOf('')\r\n                  const yeIndex = innerText.indexOf('', gongIndex)\r\n                  \r\n                  if (gongIndex !== -1 && yeIndex !== -1) {\r\n                    const textBetween = innerText.substring(gongIndex + 1, yeIndex).trim()\r\n                    totalPages = textBetween\r\n                    console.log('textBetween:', textBetween)\r\n                    const numberMatch = textBetween.match(/(\\d+)/)\r\n                    if (numberMatch) {\r\n                      totalPages = numberMatch[1]\r\n                    }\r\n                  }\r\n                  \r\n                  // /\r\n                  const hasPrev = pagerDiv.querySelector('a[href*=\"goto(\"]') && \r\n                                 pagerDiv.textContent.includes('')\r\n                  const hasNext = pagerDiv.querySelector('a[href*=\"goto(\"]') && \r\n                                 pagerDiv.textContent.includes('')\r\n                  \r\n                  return 'SUCCESS:' + currentPage + '' + totalPages + ' (:' + (hasPrev ? '' : '') + ', :' + (hasNext ? '' : '') + ')'\r\n                } catch (error) {\r\n                  return 'ERROR: - ' + error.message\r\n                }\r\n              })()\r\n            `\r\n          }\r\n        )\r\n\r\n        // \r\n        if (result && result.result && result.result.value) {\r\n          const message = result.result.value\r\n\r\n          if (message.startsWith('SUCCESS:')) {\r\n            this.$notify.info(message.substring(8)) // \"SUCCESS:\"\r\n            console.log(':', message)\r\n          } else if (message.startsWith('ERROR:')) {\r\n            this.$notify.warning(message.substring(6)) // \"ERROR:\"\r\n            console.warn(':', message)\r\n          } else {\r\n            this.$notify.error('')\r\n            console.error(':', result)\r\n          }\r\n        } else {\r\n          this.$notify.error('')\r\n          console.error(':', result)\r\n        }\r\n\r\n      } catch (error) {\r\n        console.error(':', error)\r\n\r\n        // \r\n        if (error.message && error.message.includes('Could not establish connection')) {\r\n          this.devtoolsConnected = false\r\n          this.connectionStatus = ''\r\n          this.$notify.error('DevTools')\r\n        } else {\r\n          this.$notify.error(': ' + error.message)\r\n        }\r\n      }\r\n    },\r\n    parserRoleData(data) {\r\n      const roleInfo = new window.RoleInfoParser(data.large_equip_desc, { equip_level: data.equip_level })\r\n      console.log('roleInfo:', roleInfo)\r\n      return roleInfo.result\r\n      // return {\r\n      //   RoleInfoParser: roleInfo,\r\n      //   roleInfo: roleInfo.result,\r\n      //   accept_bargain: data.accept_bargain,\r\n      //   collect_num: data.collect_num,\r\n      //   dynamic_tags: data.dynamic_tags,\r\n      //   eid: data.eid,\r\n      //   highlight: data.highlight,\r\n      //   is_split_independent_role: data.is_split_independent_role,\r\n      //   is_split_main_role: data.is_split_main_role,\r\n      //   large_equip_desc: data.large_equip_desc,\r\n      //   level: data.level,\r\n      //   other_info: data.other_info,\r\n      //   school: data.school,\r\n      //   seller_nickname: data.seller_nickname,\r\n      //   server_name: data.server_name,\r\n      //   serverid: data.serverid,\r\n      //   price: data.price,\r\n      //   sum_exp: data.sum_exp,\r\n      //   create_time: data.create_time,\r\n      //   update_time: data.create_time,\r\n      //   all_equip_json: '',\r\n      //   all_summon_json: '',\r\n      //   split_price_desc: '',\r\n      //   pet_price: '',\r\n      //   equip_price: '',\r\n      //   base_price: '',\r\n      //   history_price: '',\r\n      // }\r\n    },\r\n    parseListData(responseDataStr) {\r\n      //  Request.JSONP.request_map.request_(xxxx) xxxx\r\n      const match = responseDataStr.match(/Request\\.JSONP\\.request_map\\.request_\\d+\\((.*)\\)/)\r\n      let templateJSONStr = '{}'\r\n      if (match) {\r\n        templateJSONStr = match[1]\r\n      } else {\r\n        templateJSONStr = responseDataStr\r\n      }\r\n      try {\r\n        let templateJSON = {}\r\n        if (typeof templateJSONStr === 'string') {\r\n          templateJSON = JSON.parse(templateJSONStr)\r\n        } else {\r\n          // h5\r\n          templateJSON = templateJSONStr\r\n        }\r\n        return templateJSON\r\n      } catch (error) {\r\n        console.error(':', error)\r\n        return {}\r\n      }\r\n    },\r\n    initMessageListener() {\r\n      console.log('DevToolsPanel mounted, initializing listener')\r\n\r\n      // \r\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\r\n        // \r\n        if (window.cbgDevToolsListener) {\r\n          chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\r\n        }\r\n\r\n        // \r\n        window.cbgDevToolsListener = (request, sender, sendResponse) => {\r\n          console.log('DevToolsPanel received Chrome message:', request.action)\r\n          this.handleChromeMessage(request, sender, sendResponse)\r\n          sendResponse({ success: true })\r\n        }\r\n\r\n        // \r\n        chrome.runtime.onMessage.addListener(window.cbgDevToolsListener)\r\n        console.log('Chrome message listener registered for DevToolsPanel')\r\n      }\r\n    },\r\n\r\n    removeMessageListener() {\r\n      // Chrome\r\n      if (typeof chrome !== 'undefined' && chrome.runtime && window.cbgDevToolsListener) {\r\n        chrome.runtime.onMessage.removeListener(window.cbgDevToolsListener)\r\n        delete window.cbgDevToolsListener\r\n        console.log('Chrome message listener removed for DevToolsPanel')\r\n      }\r\n    },\r\n\r\n    checkConnectionStatus() {\r\n      // Chrome\r\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\r\n        // ping\r\n        chrome.runtime.sendMessage({ action: 'ping' }, (response) => {\r\n          if (chrome.runtime.lastError) {\r\n            console.log('Chrome extension connection check failed:', chrome.runtime.lastError)\r\n            this.devtoolsConnected = false\r\n            this.connectionStatus = ''\r\n          } else if (response && response.success) {\r\n            console.log('Chrome extension connection check successful:', response)\r\n            this.devtoolsConnected = true\r\n            this.connectionStatus = ''\r\n          } else {\r\n            console.log('Chrome extension connection check failed: invalid response')\r\n            this.devtoolsConnected = false\r\n            this.connectionStatus = ''\r\n          }\r\n        })\r\n      } else {\r\n        console.log('Chrome runtime not available')\r\n        this.devtoolsConnected = false\r\n        this.connectionStatus = 'Chrome'\r\n      }\r\n    },\r\n\r\n    handleChromeMessage(request, sender, sendResponse) {\r\n      switch (request.action) {\r\n        case 'updateRecommendData':\r\n          this.recommendData = request.data || []\r\n\r\n          // \r\n          this.recommendData.filter(item => parseListData(item.responseData)?.advance_search_type === 'overall_role_search').sort((a, b) => (b.timestamp || 0) - (a.timestamp || 0))\r\n\r\n          // \r\n          if (this.recommendData && this.recommendData.length > 0) {\r\n            this.recommendData.forEach(item => {\r\n              if (item.status === 'completed' &&\r\n                item.responseData &&\r\n                item.url &&\r\n                item.requestId &&\r\n                !this.processedRequests.has(item.requestId)) {\r\n\r\n                // \r\n                this.processedRequests.add(item.requestId)\r\n                console.log(`: ${item.requestId}`)\r\n\r\n                // \r\n                this.$api.spider.parseResponse({\r\n                  url: item.url,\r\n                  response_text: item.responseData\r\n                }).then(res => {\r\n                  console.log(` ${item.requestId} :`, res)\r\n                  if (res.code === 200) {\r\n                    console.log(` ${item.requestId} :`, res.data)\r\n                  } else {\r\n                    console.error(` ${item.requestId} :`, res.message)\r\n                  }\r\n                }).catch(error => {\r\n                  console.error(` ${item.requestId} :`, error)\r\n                  // \r\n                  this.processedRequests.delete(item.requestId)\r\n                })\r\n              }\r\n            })\r\n          }\r\n          break\r\n\r\n        case 'devtoolsConnected':\r\n          this.devtoolsConnected = true\r\n          this.connectionStatus = ''\r\n          this.$notify.success(request.message)\r\n          break\r\n\r\n        case 'showDebuggerWarning':\r\n          this.devtoolsConnected = false\r\n          this.connectionStatus = ''\r\n          this.$notify.warning(request.message)\r\n          break\r\n\r\n        case 'clearRecommendData':\r\n          this.recommendData = []\r\n          this.expandedItems = []\r\n          this.processedRequests.clear()\r\n          console.log('')\r\n          break\r\n      }\r\n    },\r\n\r\n\r\n    clearData() {\r\n      this.recommendData = []\r\n      this.expandedItems = []\r\n      // background script\r\n      if (typeof chrome !== 'undefined' && chrome.runtime) {\r\n        chrome.runtime.sendMessage({\r\n          action: 'clearRecommendData'\r\n        })\r\n      }\r\n    },\r\n\r\n    toggleResponse(index) {\r\n      const expandedIndex = this.expandedItems.indexOf(index)\r\n      if (expandedIndex > -1) {\r\n        this.expandedItems.splice(expandedIndex, 1)\r\n      } else {\r\n        this.expandedItems.push(index)\r\n      }\r\n    },\r\n\r\n    formatTime(timestamp) {\r\n      if (!timestamp) return ''\r\n      const date = new Date(timestamp)\r\n      return date.toLocaleTimeString()\r\n    },\r\n\r\n    checkIfInNewWindow() {\r\n      // \r\n      try {\r\n\r\n        // 1: chrome.devtools API\r\n        if (typeof chrome !== 'undefined' && chrome.devtools && chrome.devtools.inspectedWindow) {\r\n          this.isInNewWindow = false\r\n          console.log('ChromeSidePanelAPI')\r\n          return\r\n        }\r\n\r\n        // 2: URL - SidePanel\r\n        const currentUrl = window.location.href\r\n        if (currentUrl.includes('chrome-extension://')) {\r\n          // SidePanel\r\n          if (currentUrl.includes('panel.html')) {\r\n            // panel.htmlSidePanel\r\n            this.isInNewWindow = false\r\n            console.log('ChromeSidePanelURL')\r\n            return\r\n          } else if (currentUrl.includes('panel.html')) {\r\n            // panel.html\r\n            this.isInNewWindow = true\r\n            console.log('URL')\r\n            return\r\n          }\r\n        }\r\n\r\n        // 3: \r\n        if (document.title === '') {\r\n          // SidePanel\r\n          if (currentUrl.includes('panel.html')) {\r\n            this.isInNewWindow = false\r\n            console.log('ChromeSidePanel+URL')\r\n            return\r\n          } else {\r\n            this.isInNewWindow = true\r\n            console.log('')\r\n            return\r\n          }\r\n        }\r\n\r\n        // 4: iframe\r\n        if (window.self !== window.top) {\r\n          this.isInNewWindow = false\r\n          console.log('ChromeSidePaneliframe')\r\n          return\r\n        }\r\n\r\n        // 5: parent\r\n        if (window.parent === window) {\r\n          // \r\n          if (currentUrl.includes('panel.html')) {\r\n            this.isInNewWindow = false\r\n            console.log('ChromeSidePanelparent+URL')\r\n          } else {\r\n            this.isInNewWindow = true\r\n            console.log('parent')\r\n          }\r\n        } else {\r\n          this.isInNewWindow = false\r\n          console.log('ChromeSidePanelparent')\r\n        }\r\n\r\n      } catch (error) {\r\n        console.error(':', error)\r\n        // \r\n        this.isInNewWindow = true\r\n        console.log('')\r\n      }\r\n    },\r\n\r\n    async openInNewTab() {\r\n      try {\r\n        // \r\n        const extensionUrl = chrome.runtime.getURL('panel.html')\r\n\r\n        // chrome.tabs.create\r\n        await chrome.tabs.create({\r\n          url: extensionUrl,\r\n          active: true // \r\n        })\r\n\r\n        this.$notify.success('')\r\n\r\n      } catch (error) {\r\n        console.error(':', error)\r\n\r\n        // chrome.tabs.createwindow.open\r\n        try {\r\n          const extensionUrl = chrome.runtime.getURL('panel.html')\r\n          window.open(extensionUrl, '_blank')\r\n          this.$notify.success('')\r\n        } catch (fallbackError) {\r\n          console.error(':', fallbackError)\r\n          this.$notify.error(': ' + error.message)\r\n        }\r\n      }\r\n    },\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.panel {\r\n  box-sizing: border-box;\r\n  padding: 16px;\r\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\r\n  background: #f5f5f5;\r\n  min-height: 100vh;\r\n  background: url(~@/../public/assets/images/areabg.webp) repeat-y;\r\n  width: 960px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.panel-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 20px;\r\n  padding-bottom: 12px;\r\n  border-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n.panel-header h3 {\r\n  margin: 0;\r\n  color: #333;\r\n  font-size: 18px;\r\n}\r\n\r\n.connection-status {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.status-indicator {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n}\r\n\r\n.status-dot {\r\n  pos